;height:0;border-width:6px;border-style:solid;border-color:transparent;border-top-color:#444;-webkit-transform:translateX(-50%);-moz-transform:translateX(-50%);-ms-transform:translateX(-50%);-o-transform:translateX(-50%);transform:translateX(-50%)\n}\n.tooltip:hover::after,.tooltip:hover::before{display:block\n}\nbody{background-color:#fff\n}\n@media (max-width: 850px){\nbody{padding-top:40px\n}\n}\n.container{display:-webkit-box;display:-webkit-flex;display:-moz-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-webkit-justify-content:center;-moz-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-orient:vertical;-webkit-box-direction:normal;-webkit-flex-direction:column;-moz-box-orient:vertical;-moz-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-flex-wrap:wrap;-ms-flex-wrap:wrap;flex-wrap:wrap;padding:30px 30px 40px 30px;background-color:#f4f4f4\n}\n@media (max-width: 920px){\n.container{padding:20px 30px\n}\n}\n@media (max-width: 530px){\n.container{padding:10px 0\n}\n}\n.container .list{display:-webkit-box;display:-webkit-flex;display:-moz-box;display:-ms-flexbox;display:flex;-webkit-flex-wrap:wrap;-ms-flex-wrap:wrap;flex-wrap:wrap;margin:0 auto\n}\n.container .list>li{margin-bottom:15px;width:220px;margin:0 15px 15px 15px\n}\n@media (max-width: 577px){\n.container .list>li{margin:5px;width:-webkit-calc(50% - 10px);width:-moz-calc(50% - 10px);width:calc(50% - 10px)\n}\n.container .list>li .tools-group,.container .list>li .attribution{display:none\n}\n}\n.container .list>li:hover .badge{opacity:1\n}\n@media (min-width: 2059px){\n.container .list.small-mode{width:auto;max-width:auto;display:-webkit-box;display:-webkit-flex;display:-moz-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-webkit-justify-content:center;-moz-box-pack:center;-ms-flex-pack:center;justify-content:center\n}\n}\n@media (max-width: 2059px){\n.container .list.small-mode{max-width:1750px\n}\n}\n@media (max-width: 1830px){\n.container .list.small-mode{max-width:1500px\n}\n}\n@media (max-width: 1580px){\n.container .list.small-mode{max-width:1250px\n}\n}\n@media (max-width: 1330px){\n.container .list.small-mode{max-width:1000px\n}\n}\n@media (max-width: 1080px){\n.container .list.small-mode{max-width:750px\n}\n}\n@media (max-width: 830px){\n.container .list.small-mode{max-width:500px\n}\n}\n.container .list.large-mode li{width:400px\n}\n@media (min-width: 2280px){\n.container .list.large-mode li{width:-webkit-calc(16.6% - 30px);width:-moz-calc(16.6% - 30px);width:calc(16.6% - 30px)\n}\n}\n@media (max-width: 2279px){\n.container .list.large-mode li{width:-webkit-calc(20% - 30px);width:-moz-calc(20% - 30px);width:calc(20% - 30px)\n}\n}\n@media (max-width: 1910px){\n.container .list.large-mode li{width:-webkit-calc(25% - 30px);width:-moz-calc(25% - 30px);width:calc(25% - 30px)\n}\n}\n@media (max-width: 1530px){\n.container .list.large-mode li{width:-webkit-calc(33% - 30px);width:-moz-calc(33% - 30px);width:calc(33% - 30px)\n}\n}\n@media (max-width: 1177px){\n.container .list.large-mode li{width:-webkit-calc(50% - 30px);width:-moz-calc(50% - 30px);width:calc(50% - 30px)\n}\n}\n@media (max-width: 577px){\n.container .list.large-mode li{margin:15px;width:-webkit-calc(100% - 30px);width:-moz-calc(100% - 30px);width:calc(100% - 30px)\n}\n}\n.container .list.contracted .tools-group,.container .list.contracted .attribution{display:none\n}\n.container .card{-webkit-box-shadow:0 0 1px 0px rgba(0,0,0,0.16);-moz-box-shadow:0 0 1px 0px rgba(0,0,0,0.16);box-shadow:0 0 1px 0px rgba(0,0,0,0.16);width:100%;display:-webkit-box;display:-webkit-flex;display:-moz-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-webkit-flex-direction:column;-moz-box-orient:vertical;-moz-box-direction:normal;-ms-flex-direction:column;flex-direction:column;background-color:#fffffb;padding:10px\n}\n.main{position:relative;cursor:pointer;height:0;overflow:hidden;text-align:center;padding-bottom:75%\n}\n.main img{max-width:100%\n}\n.main .gif-indicator{position:absolute;top:6px;right:6px;width:23px;height:14px;opacity:.75;background-position-y:0px;background-image:url(\"../assets/gif-indicator.png\")\n}\n.main .gif-indicator.isLoading{-webkit-animation:loading 1s steps(1) infinite 0.2s;-moz-animation:loading 1s steps(1) infinite 0.2s;-o-animation:loading 1s steps(1) infinite 0.2s;animation:loading 1s steps(1) infinite 0.2s\n}\n@-webkit-keyframes loading{\n0%{background-position-y:56px\n}\n33.3%{background-position-y:42px\n}\n66.6%{background-position-y:28px\n}\n100%{background-position-y:14px\n}\n}\n@-moz-keyframes loading{\n0%{background-position-y:56px\n}\n33.3%{background-position-y:42px\n}\n66.6%{background-position-y:28px\n}\n100%{background-position-y:14px\n}\n}\n@-o-keyframes loading{\n0%{background-position-y:56px\n}\n33.3%{background-position-y:42px\n}\n66.6%{background-position-y:28px\n}\n100%{background-position-y:14px\n}\n}\n@keyframes loading{\n0%{background-position-y:56px\n}\n33.3%{background-position-y:42px\n}\n66.6%{background-position-y:28px\n}\n100%{background-position-y:14px\n}\n}\n.main .gif-indicator-target{position:absolute;top:0;right:0;width:100%;height:152px;cursor:pointer;z-index:999\n}\n.small-mode .main .gif-indicator-target{height:80px\n}\n.main .info{position:absolute;left:0;top:0;width:100%;height:100%;padding:15px;opacity:0;display:-webkit-box;display:-webkit-flex;display:-moz-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-webkit-flex-direction:column;-moz-box-orient:vertical;-moz-box-direction:normal;-ms-flex-direction:column;flex-direction:column;z-index:99;background-color:rgba(255,255,255,0.96);-webkit-transition:all 0.25s;-o-transition:all 0.25s;-moz-transition:all 0.25s;transition:all 0.25s\n}\n.main .info strong{font-weight:500;font-size:14px;line-height:1.2;color:#444;white-space:nowrap;-o-text-overflow:ellipsis;text-overflow:ellipsis;overflow:hidden\n}\n.mai